import { Scramble } from '@/helpers/scrumble'

const TextScramble = {
  bind(el, binding) {
    let data = binding.value

    const fx = new Scramble(el)
    
    let counter = 0
    const next = () => {
      fx.setText(data[counter]).then(() => {
        setTimeout(next, 4000)
      })
      counter = (counter + 1) % data.length
    }
    next()
  }
}

const CropHighText = {
  bind(el, binding, vnode) {
    let data = binding.value
    if (data.on)
      return
    vnode.context.$nextTick(() => {
      if (el.offsetHeight >= data.height) {
          el.classList.add(data.class)
          el.style.maxHeight = data.height + 'px'
      }
    })
  }
}

export default { TextScramble, CropHighText }
