function getTitle (vm) {
  const { title } = vm.$options
  if (title) {
    return typeof title === 'function' ? title.call(vm) : title
  }
}
  
const metaTitle = {
  created () {
    const title = getTitle(this)
    if (title) document.title = title
  },
  watch: {
    title() {
      const title = getTitle(this)
      if (title) document.title = title
    }
  }
}

export default { metaTitle }
