<template>
  <div id="app">
    <site-content>
      <router-view />
    </site-content>
    <site-footer :content="footer" />
  </div>
</template>

<script>
import { SiteContent, SiteFooter } from '@/components/site'

export default {
  name: 'App',
  components: { SiteContent, SiteFooter },
  data() {
    return {
      footer: {
        copy: 'FIE Lain.ee &copy; 2020',
        navigation: [],
        contacts: {
          address: '3 Kreenholmi Str., Ida-Virumaa 21020, Estonia',
          telephone: '+372253426145',
          email: 'info@lain.ee'
        }
      }
    }
  },
  created() {
    this.$router.options.routes
        .filter(route => route.meta?.showInFooter)
        .forEach(route => this.footer.navigation.push({ name: route.meta?.name, path: route.name }))
  }
}
</script>

