import Vue from 'vue'
import VueRouter from 'vue-router'
import { Home, Services } from '@/views'

Vue.use(VueRouter)

const routes = [
  { path: '/', name: 'home', component: Home, meta: {
    name: 'Veebistuudio'
  } },

  { path: '/portfolio', name: 'portfolio', component: Home, meta: {
    name: 'Portfell',
    showInFooter: true
  } },

  { path: '/services', name: 'services', component: Services, meta: {
    name: 'Teenused',
    showInFooter: true
  } },

  { path: '/solutions', name: 'solutions', component: Home, meta: {
    name: 'Lahendused',
    showInFooter: true
  } },

  { path: '/contacts', name: 'contacts', component: Home, meta: {
    name: 'Kontaktid',
    showInFooter: true
  } },

  { path: '/career', name: 'career', component: Home, meta: {
    name: 'Karjäär',
    showInFooter: true
  } },
  { path: '*', redirect: '/' },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: routes
})

export default router